@charset "UTF-8";
/*****GLOBALS*****/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: "Noto Sans", sans-serif;
  color: #000 !important;
}

section {
  padding: 70px 1.5rem;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 5px;
}

table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

tr td {
  width: 33.33%;
  position: relative;
}

.fa-check {
  color: #a7ca1a;
  font-size: 18px;
}

.best-value {
  background-color: #a7ca1a;
  color: white;
  position: absolute;
  top: -1rem;
  left: 0;
  width: 100%;
  padding: 0.2rem 1rem;
}

/****BUTTONS*****/
.button {
  display: inline-block;
  padding: 0.6rem 1rem;
  width: 80%;
  text-align: center;
  background-color: #f7f7f7;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  border-radius: 6px;
  transition: all 300ms ease;
  margin-bottom: 0.8rem;
}

.button.button-purple {
  background-color: #7d6e99;
}
.button.button-purple:hover {
  background-color: #6f6288;
}

.button.button-green {
  background-color: #a7ca1a;
}
.button.button-green:hover {
  background-color: #99b916;
}

.button.button-blue {
  background-color: #4c7da5;
}
.button.button-blue:hover {
  background-color: #426d91;
}

/*****FONT SIZES*****/
h1 {
  font-size: 36px;
  font-weight: 900;
  font-family: "Montserrat", sans-serif;
  color: #636363;
}

h2 {
  font-size: 18px;
  font-weight: 900;
  font-family: "Montserrat", sans-serif;
  color: #000;
}

/*****TOP*****/
table.top td {
  padding: 2rem 1.5rem;
  text-align: center;
  text-transform: capitalize;
  border: 2px solid #f7f7f7;
}
@media (max-width: 768px) {
  table.top td {
    display: block;
    width: 100%;
  }
}
@media (max-width: 970px) {
  table.top td:first-child {
    display: none;
  }
}
table.top td p.toggle {
  margin: 0;
  padding: 0.5rem;
  cursor: pointer;
  display: none;
  position: relative;
  transition: all ease-in 300ms;
}
table.top td p.toggle::before {
  content: "";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: relative;
  color: #636363;
  margin-right: 0.4rem;
  transition: all ease-in 300ms;
}
table.top td p.toggle span {
  color: #a7ca1a;
  margin-left: 0.2rem;
}
@media (max-width: 970px) {
  table.top td p.toggle {
    display: block;
  }
}
table.top td .show-content {
  display: none;
  margin-bottom: 0.3rem;
}
table.top td .show-content.visible {
  display: flex;
  justify-content: space-evenly;
}
table.top td .show-content.visible p {
  margin: 0;
}
table.top td h2 {
  margin-bottom: 10px;
}
table.top td h1 {
  margin-bottom: 10px;
}
table.top td p {
  font-size: 14px;
  color: #636363;
  margin-bottom: 25px;
}

/*****MIDDLE*****/
@media (max-width: 970px) {
  table.middle {
    display: none;
  }
}
table.middle td {
  border: 2px solid #f7f7f7;
}
table.middle thead {
  background-color: #f7f7f7;
}
table.middle thead th {
  padding: 0.7rem 3rem;
  text-align: center;
  text-transform: capitalize;
}
table.middle thead th.control {
  text-align: left;
  cursor: pointer;
  position: relative;
}
table.middle thead th.control:hover {
  background-color: #f0eeee;
}
table.middle thead th.control::before {
  content: "";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: absolute;
  left: 1.3rem;
  color: #636363;
  transition: all ease-in 300ms;
}
table.middle thead th.control span {
  color: #a7ca1a;
  margin-left: 0.3rem;
}
table.middle thead th.control.active::before {
  transform: rotate(-180deg);
}
table.middle tbody {
  background-color: #fff;
  visibility: collapse;
  color: #636363 !important;
}
table.middle tbody td {
  padding: 0.7rem 3rem;
  text-align: center;
}
table.middle tbody td:first-child {
  text-align: left;
}
table.middle tbody.visible {
  visibility: visible;
}

/*****BOTTOM*****/
@media (max-width: 768px) {
  table.bottom {
    display: none;
  }
}
table.bottom td {
  padding: 2rem 1.5rem;
  text-align: center;
  text-transform: capitalize;
  border: 2px solid #f7f7f7;
}
@media (max-width: 970px) {
  table.bottom td:first-child {
    display: none;
  }
}